@use "../scss/vars" as v;
//works
html {
  font-family: Roboto, sans-serif;
  margin: 0;
  height: 100%;
  overflow: hidden;
  //scroll-behavior: smooth;
}
body {
  font-family: Roboto, sans-serif;
    color: v.$primary-color;
    background-image: url(v.$background-image);
    -webkit-backdrop-filter: blur(1.5px);
            backdrop-filter: blur(1.5px);
    overflow: hidden;
    height: 100%;
    margin: 0;
    @include v.center;
  }
///////////////////////////////////////
//building-blocks//
///////////////////////////////////////


.btn-primary {
    background-color: v.$secondary-color;
    border-color: v.$secondary-color;
    color: v.$primary-color;
    transition: transform 0.3s;
}
.btn-primary:hover {
    transform: scale(1.05);
    background-color: v.$secondary-color;
    border-color: v.$secondary-color;
    color: v.$primary-color;
}
.btn-primary:active {
    background-color: v.$secondary-color;
    border-color: v.$secondary-color;
    color: v.$primary-color;
}
a{
  color: v.$primary-color;
  text-decoration: none;
}
.card {
  box-shadow: 15px 15px 15px -3px rgb(0 0 0 / 28%), -15px 15px 15px -3px rgb(0 0 0 / 28%);
  height: 100%;
}
.card-text {
  font-size: 0.8em;
  }
//pages
///////////////////////////////////////
//p1//
///////////////////////////////////////

.p1 {
  background-color: #141114;
  background-image: linear-gradient(335deg, black 23px, transparent 23px),
    linear-gradient(155deg, black 23px, transparent 23px),
    linear-gradient(335deg, black 23px, transparent 23px),
    linear-gradient(155deg, black 23px, transparent 23px);
  background-size: 58px 58px;
  background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
  background-repeat: repeat;
  .container-xxl {
    height: 80%;
      }
  .img {
    background-image: url(v.$logo-image);
    height: 400px;
    @include v.center;
  }
  .sign {
    @include v.flex;
  }
  //p1-media-q
  @media screen and (max-height: 850px){
    .img {
      height:295px;
    }
  }
}
///////////////////////////////////////
//p2//
///////////////////////////////////////
.p2 {
  background: conic-gradient(from 90deg at 40% -25%, #ffd700, #f79d03, #ee6907, #e6390a, #de0d0d, #d61039, #cf1261, #c71585, #cf1261, #d61039, #de0d0d, #ee6907, #f79d03, #ffd700, #ffd700, #ffd700);
  .tab {
    display: none;
  }
  //p2-media-q
  @media screen and (max-height: 2000px) {
    .card-text {
        font-size: 0.7em;
      }
    }
  
  @media screen and (max-width: 950px ) {
  .card-text {
      font-size: 0.6em;
    }
    .tab {
      display: block;
      height: 90%;
        
    .row:nth-child(2) {
      margin-top: 1rem;
        }
      }
    .desk {
      display: none;
     }
    }
  }

///////////////////////////////////////
//p3//
///////////////////////////////////////

.p3 {
  background: conic-gradient(from -90deg at -10% center, #ffa500, #ff7715, #ff522a, #ff3f47, #ff5482, #ff69b4);
  //p3-media-q 
  @media screen and (max-height: 2000px) {
    .card-text{
        font-size: 0.7em;
    
  }
    @media screen and (max-height: 1200px) {
      .card-text{
        font-size: 0.6em;
       }
      }
    
  @media screen and (max-height: 950px) {
   .card-img-top{
     display: none;
   }
  } 
}
}
///////////////////////////////////////
//p4//
///////////////////////////////////////
.p4{
  background: conic-gradient(from 90deg at 25% -10%, #ff4500, #d3f340, #7bee85, #afeeee, #7bee85)
}

///////////////////////////////////////
//p5//
///////////////////////////////////////


.p5{
  background:conic-gradient(from 270deg at 75% 110%, lawngreen, midnightblue)
} 